import * as Yup from "yup";
import { getI18n } from "react-i18next";
import { checkStringRange } from "../utils";

//note:
// /^[A-Za-z0-9 ]+$/ "true",
// /[^A-Za-z0-9]+/g "false"

export const REVIEW = {
  NOT_SATISFIED: 1,
  NORMAL: 2,
  SATISFIED: 3,
};

export const SOCKET = {
  API_URL: `${process.env.REACT_APP_SOCKET_URL}`,
};

export const PARAMS = {
  PASSWORD: "password",
  PASSWORD_NEW: "passwordNew",
  PASSWORD_CONFIRM: "passwordConfirm",
  EMAIL: "email",
  NAME: "name",
  CONTACT: "contact",
  VOTE: "vote",
};

export const validateFormChangePassword = Yup.object({
  [PARAMS.PASSWORD]: Yup.string()
    .required(getI18n().t("Vui lng nhp mt khu c"))
    .max(255, "Vui lng nhp di 255 k t"),
  [PARAMS.PASSWORD_NEW]: Yup.string()
    .required("Vui lng nhp mt khu mi")
    .min(6, "Vui lng nhp t nht 6 k t")
    .notOneOf([Yup.ref("password"), null], "Mt khu mi khng c trng")
    .max(27, "Vui lng nhp di 27 k t"),
  [PARAMS.PASSWORD_CONFIRM]: Yup.string()
    .required("Vui lng nhp li mt khu mi")
    .oneOf([Yup.ref("passwordNew"), null], "Mt khu xc nhn khng trng khp")
    .min(6, "Vui lng nhp t nht 6 k t")
    .max(27, "Vui lng nhp di 27 k t"),
});

export const validateFormCreateNickName = Yup.object({
  userName: Yup.string()
    .matches(/^[A-Za-z0-9 ]+$/, getI18n().t("msg.onlyLetterNumber"))
    .test("len",  getI18n().t("msg.rangeNickName"), val => checkStringRange(val, 2, 25)),
  userEmail: Yup.string()
    .required(getI18n().t("msg.msgValidateRequired"))
    .email(getI18n().t("msg.msgValidateEmail")) 
});